<template>
  <div id="detection-field">
    <img src="../../../assets/images/quality/title_jcqsfx.png">


    <dv-loading v-if="Loading" style="">数据加载中...</dv-loading>
    <Echart id="ComplaintUser" ref="ComplaintUserRef" :options="options" height="200px" width="380px" />
  </div>
</template>

<script>
import Echart from '@/common/echart'

export default {
  data () {
    return {
      options: {
      },
      Loading: false
    }
  },
  components: {
    Echart,
  },
  mounted () {
    this.initChart()
  },
  props: {
    // NewTrend: {
    //   type: Array,
    //   require: true,
    //   default: () => { }
    // },
    // OldTrend: {
    //   type: Array,
    //   require: true,
    //   default: () => { }
    // },
  },

  watch: {
    // OldTrend: {
    //   handler (val) {
    //     if (val && val[0]) {
    //       this.Loading = false
    //       this.initChart()
    //     }
    //   },
    //   immediate: true,
    //   deep: true,
    // },
  },

  methods: {
    initChart () {
      // var color = ['#0CD2E6', '#3751E6', '#FFC722', '#886EFF', '#008DEC', '#114C90', '#00BFA5']
      var legend = ['政府用户', '客户', '员工']
      var seriesData = [
        { name: '政府用户', value: 24, rate: 53 },
        { name: '客户', value: 12, rate: 27 },
        { name: '员工', value: 9, rate: 20 },

      ]

      this.options = {
        // color: color,
        grid: {
          top: '15%',
          left: 0,
          right: '0%',
          bottom: 5,
          containLabel: true,
        },
        legend: {
          orient: 'vertical',
          top: 'center',
          right: 0,
          textStyle: {
            align: 'left',
            verticalAlign: 'middle',
            rich: {
              name: {
                color: '#F4F4F4',
                fontSize: 15,
              },
              value: {
                color: '#F4F4F4',
                fontSize: 20,
              },
              rate: {
                color: '#F4F4F4',
                fontSize: 20,
              },
            },
          },
          data: legend,
          formatter: (name) => {
            if (seriesData.length) {
              const item = seriesData.filter((item) => item.name === name)[0]
              if (seriesData.length) {
                const item = seriesData.filter((item) => item.name === name)[0]
                if (item.name.length == 2 && item.value < 10)
                  return `{name|${name}\t\t\t\t\t\t\t\t}{value| ${item.value}\t\t\t} {rate| ${item.rate}%}`
                else if (item.name.length == 2 && item.value > 10)
                  return `{name|${name}\t\t\t\t\t\t\t\t}{value| ${item.value}\t} {rate| ${item.rate}%}`
                else
                  return `{name|${name}\t}{value| ${item.value}\t} {rate| ${item.rate}%}`

              }
            }
          },
        },
        series: [
          {
            name: '需求类型占比',
            type: 'pie',
            center: ['25%', '50%'],
            radius: ['50%', '70%'],
            label: {
              normal: {
                show: true,
                position: 'center',
                formatter () {
                  return `45\n\n投诉用户分析`
                },
                rich: {
                  //中间文字
                  value: {
                    padding: 5,
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 20,
                  },
                  label: {
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 16,
                  },
                },
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                },
              },
              // tooltip: {
              //   show: true,
              //   trigger: "item",
              //   formatter: "{b} :  {c}"
              // },
            },

            // labelLine: {
            //   show: false,
            //   length: 0,
            //   length2: 0,
            // },
            itemStyle: {//间隔
              normal: {
                borderWidth: 5,
                borderColor: '#050e31',
              },
            },
            data: seriesData,
          },
        ],
      }
    },
  }
}
</script>

<style lang="scss" scoped>
#detection-field {
  width: 400px;
  height: 289px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
}
</style>
