<template>
  <div id="detection-field">
    <img src="../../../assets/images/quality/title_jcly.png">
    <!-- <Echart id="DetectionFieldChart" ref="DetectionFieldRef" :options="options" height="440px" width="566px" /> -->
    <div class="report-statics-body">
      <dv-loading v-if="Loading" style="">数据加载中...</dv-loading>
      <div class="total-data">
        <div>
          <div style="font-size:16px;font-weight: 600;line-height: 28px;">统计数据</div>
          <div style="font-size: 14px;">累计可疑性报告</div>
        </div>
        <div>
          <span style="color:#FFE600;font-size: 28px;font-weight: 600;margin-right: 10px;">17</span>条
        </div>
      </div>
      <Echart id="ReportChart" ref="ReportChartRef" :options="options" height="300px" width="520px" />
    </div>
  </div>
</template>

<script>
import Echart from '@/common/echart'
import echartImg from "@/assets/images/quality/icon_xiangmu.png"

export default {
  data () {
    return {
      config: {
        data: [],
        rowNum: 10,
        waitTime: 3000,
        valueFormatter ({ value }) {
          return (value * 100).toFixed(2) + '%'
        }
      },
      randomKey: Math.random(),
      Loading: false,
      options: {}
    }
  },
  components: {
    Echart,
  },

  props: {
    ApiData: {
      type: Array,
      require: true,
      default: () => { }
    },
  },

  // watch: {
  //   ApiData: {
  //     handler (val) {
  //       if (val && val[0]) {
  //         this.config.data = val
  //         this.randomKey = Math.random() //刷新组件
  //         this.Loading = false
  //       }
  //     },
  //     immediate: true,
  //     deep: true,
  //   },
  // },

  mounted () {
    this.initChart()
    const myChart = this.$echarts.init(document.getElementById('ReportChart'))
    myChart.on('legendselectchanged', (params) => {
      this.$router.push({ path: '/dubiousReportList/list' })
    })
  },

  methods: {
    initChart () {
      var color = ['#0CD2E6', '#3751E6', '#FFC722', '#886EFF', '#008DEC', '#114C90', '#00BFA5']
      var legend = ['缺少部分检测流程', '未存在仪器使用记录', '多组实验数据完全一致', '未存在仪器借用记录', '实验周期低于检测周期', '未存在实验现场拍照', '不符合多个指标相关性原理', '未存在车辆使用记录']
      var seriesData = [
        { name: '缺少部分检测流程', value: 5 },
        { name: '未存在仪器使用记录', value: 2 },
        { name: '多组实验数据完全一致', value: 3 },
        { name: '未存在仪器借用记录', value: 1 },
        { name: '实验周期低于检测周期', value: 2 },
        { name: '未存在实验现场拍照', value: 1 },
        { name: '不符合多个指标相关性原理', value: 2 },
        { name: '未存在车辆使用记录', value: 1 },
      ]

      this.options = {
        color: color,
        grid: {
          top: '15%',
          left: 0,
          right: '0%',
          bottom: 5,
          containLabel: true,
        },
        graphic: {//图形中间图片
          elements: [{
            type: "image",
            style: {
              image: echartImg,
              width: 49,
              height: 49,
            },
            left: "21%",
            top: "35%",
          }],
        },

        legend: {
          orient: 'vertical',
          top: 'center',
          right: 0,
          textStyle: {
            align: 'left',
            verticalAlign: 'middle',
            rich: {
              name: {
                color: '#F4F4F4',
                fontSize: 15,
              },
              value: {
                color: '#F4F4F4',
                fontSize: 20,
              },
              // rate: {
              //   color: 'rgba(255,255,255,0.9)',
              //   fontSize: 10,
              // },
            },
          },
          data: legend,
          formatter: (name) => {
            if (seriesData.length) {
              const item = seriesData.filter((item) => item.name === name)[0]
              return `{name|${name}：}{value| ${item.value}} `
            }
          },
        },
        series: [
          {
            name: '需求类型占比',
            type: 'pie',
            center: ['25%', '50%'],
            radius: ['60%', '70%'],
            label: {
              normal: {
                show: true,
                position: 'center',
                // formatter: '{value|{c}}\n{label|{b}}',
                formatter () {
                  // console.log(value,111)
                  // console.log(label)
                  return `\n\n\n可疑性报告情况`
                },
                rich: {
                  //中间文字
                  value: {
                    padding: 5,
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 20,
                  },
                  label: {
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 16,
                  },
                },
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                },
              },
            },
            // labelLine: {
            //   show: false,
            //   length: 0,
            //   length2: 0,
            // },
            itemStyle: {//间隔
              normal: {
                borderWidth: 5,
                borderColor: '#050e31',
              },
            },
            data: seriesData,
          },
        ],
      }
    },
  }
}
</script>

<style lang="scss" scoped>
#detection-field {
  width: 566px;
  height: 512px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .report-statics-body {
    height: 480px;
    padding: 10px 10px 10px 20px;

    .total-data {
      width: 534px;
      height: 91px;
      background: url("~@/assets/images/quality/bg01.png") no-repeat;
      color: #fff;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 30px;
    }
  }

}


::v-deep .rank {
  color: #E9E9E9 !important;
}

// ::v-deep .dv-scroll-ranking-board .ranking-column .inside-column {
//   background: linear-gradient(270deg, rgba(15, 66, 115, 1), rgba(93, 194, 255, 1));
// }

::v-deep .dv-scroll-ranking-board .row-item:nth-child(odd)>.ranking-column .inside-column {
  background: linear-gradient(270deg, rgba(15, 66, 115, 1), rgba(93, 194, 255, 1));
  // background: linear-gradient(to right, rgba(15, 66, 115, 1) 55%, #002947 0, rgba(15, 66, 115, 1) 60%, #002947 0, rgba(15, 66, 115, 1) 70%, #002947 0);
  // background-size: 20px 20px;
}

::v-deep .dv-scroll-ranking-board .row-item:nth-child(even)>.ranking-column .inside-column {
  background: linear-gradient(270deg, rgba(115, 75, 15, 1), rgba(255, 172, 93, 1));
  // background-size: 20px 20px;
}


::v-deep .dv-scroll-ranking-board .ranking-column {
  background-color: #002947;
  border-bottom: 0;
}
</style>
