<template>
  <div id="detection-users">
    <img src="../../../assets/images/quality/title_jcqsfx(2).png">
    <Echart id="ComplaintUser" ref="ComplaintUserRef" :options="options" height="200px" width="380px" />
  </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
  data () {
    return {
      startVal: 0,
      duration: 1000,
      options: {}
    }
  },
  components: {
    Echart,
  },
  mounted () {
    this.initChart()
  },
  methods: {
    initChart () {
      const colors = ["#00D24F", "#FFBC00", "#F08C6A", "#E74745", "#49C6F2"]
      var legend = ['内部评审', '外部评审', '管理评审', '内部质量抽查', '外部质量抽查']
      var seriesData = [
        { name: '内部评审', value: 37, rate: 44 },
        { name: '外部评审', value: 32, rate: 38 },
        { name: '管理评审', value: 5, rate: 6 },
        { name: '内部质量抽查', value: 5, rate: 6 },
        { name: '外部质量抽查', value: 5, rate: 6 },

      ]

      this.options = {
        color: colors,
        grid: {
          top: '15%',
          left: 0,
          right: '0%',
          bottom: 5,
          containLabel: true,
        },
        legend: {
          orient: 'vertical',
          top: 'center',
          right: 0,
          textStyle: {
            align: 'left',
            verticalAlign: 'middle',
            rich: {
              name: {
                color: '#F4F4F4',
                fontSize: 15,
              },
              value: {
                color: '#F4F4F4',
                fontSize: 20,
              },
              rate: {
                color: '#F4F4F4',
                fontSize: 20,
              },
            },
          },
          data: legend,
          formatter: (name) => {
            if (seriesData.length) {
              const item = seriesData.filter((item) => item.name === name)[0]
              if (item.name.length == 4 && item.value >= 10)
                return `{name|${name}\t\t\t\t}{value| ${item.value}\t} {rate| ${item.rate}%}`
              else if (item.name.length == 4 && item.value < 10)
                return `{name|${name}\t\t\t\t}{value| ${item.value}\t\t\t} {rate| ${item.rate}%}`
              else
                return `{name|${name}\t}{value| ${item.value}\t} {rate| ${item.rate}%}`
            }
          },
        },
        series: [
          {
            name: '需求类型占比',
            type: 'pie',
            center: ['22%', '50%'],
            radius: ['50%', '70%'],
            label: {
              normal: {
                show: true,
                position: 'center',
                // formatter: '{value|{c}}\n{label|{b}}',
                formatter () {
                  // console.log(value,111)
                  // console.log(label)
                  return `84\n不符合项来源分析`
                },
                rich: {
                  //中间文字
                  value: {
                    padding: 5,
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 20,
                  },
                  label: {
                    align: 'center',
                    verticalAlign: 'middle',
                    fontSize: 16,
                  },
                },
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                },
              },
              
            },
            // labelLine: {
            //   show: false,
            //   length: 0,
            //   length2: 0,
            // },
            itemStyle: {//间隔
              normal: {
                borderWidth: 5,
                borderColor: '#050e31',
              },
            },
            data: seriesData,
          },
        ],
      }
    },
  }
}
</script>

<style lang="scss" scoped>
#detection-users {
  width: 400px;
  height: 289px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .users-body {
    padding: 15px 10px 0 18px;
    display: grid;
    grid-template-columns: repeat(3, 32%);
    // grid-template-rows: repeat(3, 33.33%);
    grid-row-gap: 30px;
    grid-column-gap: 10px;

    .data-item {
      height: 70px;
      display: flex;

      .users-img {
        width: 72px;
        height: 72px;
        margin-right: 10px;
      }

      .users-info {
        color: #DADFE4;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .users-num-blue {
          color: #96CEFF;
          font-size: 24px;
          font-weight: 550;
          margin-right: 10px;
        }

        .users-num-green {
          color: #14DFE0;
          font-size: 24px;
          font-weight: 550;
          margin-right: 10px;
        }


      }
    }

  }
}
</style>
