<template>
  <div id="detection" ref="appRef">
    <div class="bg">
      <video src="../../assets/bg.mp4" class="bjimg" autoplay loop muted></video>
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div v-else class="host-body">
        <Header headerType="quality" />
        <div class="echart-body">
          <div class="body-left">
            <detection-aptitude class="body-left-top" />
            <detection-field />
          </div>

          <div class="body-right">
            <data-detail class="body-right-top" />
            <div style="display:flex;justify-content: space-between;">

              <complaint-user />
              <complaint-type />

              <complaint-source />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { DetectionFieldApi, queryDetectionTrendApi } from "@/api/detection.js"
import {
  BussinessOverviewApi
} from "@/api/home.js"

import drawMixin from "@/utils/drawMixin"

import Header from "@/components/Header"
import DetectionAptitude from "./components/DetectionAptitude.vue"
import DetectionField from "./components/DetectionField.vue"
import DataDetail from "./components/DataDetail.vue"
import ComplaintUser from "./components/ComplaintUser.vue"
import ComplaintSource from "./components/ComplaintSource.vue"
import ComplaintType from "./components/ComplaintType.vue"

export default {
  mixins: [drawMixin],
  data () {
    return {
      loading: true,
      detectionFieldData: [],
      oldTrendData: [],
      newTrendData: [],
      TesterCount: 0
    }
  },
  components: {
    Header, DetectionAptitude, DetectionField, DataDetail, ComplaintUser, ComplaintSource, ComplaintType
  },
  mounted () {
    this.cancelLoading()
    DetectionFieldApi().then(res => {
      const { code, data } = res
      if (code == 0) {
        this.detectionFieldData = data
        // this.detectionFieldData.forEach(item => {
        //   item.value = (item.value * 100).toFixed(2)
        // })
      }
    })

    BussinessOverviewApi().then(res => {
      const { code, data } = res
      if (code == 0) {
        this.TesterCount = data.testerAmount
      }
    })

    queryDetectionTrendApi(1).then(res => {
      const { code, data } = res
      if (code == 0) {
        this.newTrendData = data.map(item => item.value)
        queryDetectionTrendApi(2).then(res => {
          const { code, data } = res
          if (code == 0) {
            this.oldTrendData = data.map(item => item.value)
          }
        })
      }
    })


  },
  beforeDestroy () {
    clearInterval(this.timing)
  },
  methods: {
    cancelLoading () {
      setTimeout(() => {
        this.loading = false
      }, 500)
    }
  }
}
</script>

<style lang="scss" scoped>
// @import '@/assets/scss/index.scss';
#detection {
  color: #d3d6dd;
  width: 1920px;
  height: 1080px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform-origin: left top;
  overflow: hidden;

  .bg {
    width: 100%;
    height: 100%;
    // background-image: url("~@/assets/images/bg_2.png");
    // background-size: cover;
    // background-position: center center;
  }

  .bjimg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    object-fit: cover;
  }
}

.echart-body {
  margin-top: 14px;
  padding: 0 32px;
  display: flex;
  justify-content: space-between;

  .body-left {

    .body-left-top {
      margin-bottom: 17px;
    }
  }

  .body-right {

    .body-right-top {
      margin-bottom: 20px;
    }
  }

}
</style>
